.nav-item {
    transition: color 0.2s;
}

.nav-item:hover {
    color: #7A85B1;
}

.resume-nav-item a {
    text-decoration: none;
    padding: 5px;
    border: solid 1px #E09DF5;
    color: #E09DF5;
    border-radius: 7px;
    transition: color 0.2s;
    transition: background 0.2s;
}

.resume-nav-item a:hover {
    background-color: #E09DF5;
    color: white;
}